import window from 'global/window';
import _ from 'lodash';
import moment from 'moment';
import React, { Component, Fragment } from 'react';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';
import { /* Prompt, */ withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import Page from 'components/Page/Page';
import Loadable from 'react-loadable';

import { setInsuranceFormState } from 'actions/insuranceActions';
import {
  FORMSTATES,
  PARTIALFORMSTATES,
  getForm,
  getSession,
  postSession,
  putSessions,
  postQuote,
  getSummary,
  postCheckout,
  postBankTransfer,
  setGetQuoteNavbar,
  verifyOTP,
  setQuoteData,
  postComeBackLater,
} from 'actions/businessAction';
import { setCategoryNavBar } from 'actions/navActions';

import FlowHeaderImg from 'assets/images/Business/Flow/Flow.svg';

// import StepProgressBar from 'components/Templates/StepProgressBar/StepProgressBarNew';
import RefreshTimer from 'components/Templates/RefreshTimer/RefreshTimer';
// import LoadingScreenV2 from 'components/Templates/LoadingScreenV2/LoadingScreenV2';

// import BusinessLanding from 'components/Business/BusinessLanding/BusinessLanding';
// import BusinessForm from 'components/Business/BusinessForm/BusinessForm';
// import BusinessCheckout from 'components/Business/BusinessCheckout/BusinessCheckout';

import { routeHandler, getFieldsFromForms, generateRequestData } from 'lib/helperFunctions';
import { validateFormValue, validateForm } from 'lib/validatorsV2';


const StepProgressBar = Loadable({
  loader: () => import(/* webpackChunkName: "StepProgressBarNew" */ 'components/Templates/StepProgressBar/StepProgressBarNew'),
  loading: () => null,
  modules: ['StepProgressBarNew']
});

const BusinessLanding = Loadable({
  loader: () => import(/* webpackChunkName: "BusinessLanding" */ 'components/Business/BusinessLanding/BusinessLanding'),
  loading: () => null,
  modules: ['BusinessLanding']
});

const BusinessForm = Loadable({
  loader: () => import(/* webpackChunkName: "BusinessForm" */ 'components/Business/BusinessForm/BusinessForm'),
  loading: () => null,
  modules: ['BusinessForm']
});

const BusinessCheckout = Loadable({
  loader: () => import(/* webpackChunkName: "BusinessCheckout" */ 'components/Business/BusinessCheckout/BusinessCheckout'),
  loading: () => null,
  modules: ['BusinessCheckout']
});

const LoadingScreenV2 = Loadable({
  loader: () => import(/* webpackChunkName: "LoadingScreenV2" */ 'components/Templates/LoadingScreenV2/LoadingScreenV2'),
  loading: () => null,
  modules: ['LoadingScreenV2']
});

const routeName = 'business';

const emailField = {
  slug: 'email',
  placeholderString: 'Enter company email address',
  type: 'email',
  is_required: true,
  size: 12,
};

const checkoutFields = [{
  title: 'Contact Details',
  forms: [{
    size: 12,
    title: 'Email',
    slug: 'email',
    placeholderString: 'Enter company email address',
    type: 'email',
    readOnly: true,
    is_required: true,
  }, {
    size: 12,
    title: 'Company',
    slug: 'company',
    placeholder: 'E.g Singtel',
    type: 'text',
    is_required: true,
  }, {
    size: 12,
    title: 'First name',
    slug: 'first_name',
    placeholder: 'E.g John',
    type: 'text',
    is_required: true,
  }, {
    size: 12,
    title: 'Last name',
    slug: 'last_name',
    placeholder: 'E.g Doe',
    type: 'text',
    is_required: true,
  }, {
    size: 12,
    title: 'Mobile no',
    slug: 'mobile_number',
    placeholder: 'E.g 8888 8888',
    type: 'number',
    is_required: true,
  }],
}, {
  title: 'Plan Details',
  forms: [{
    size: 12,
    title: 'No of employees',
    slug: 'no_of_employees',
    placeholder: 'No of employees',
    type: 'text',
    readOnly: true,
  }, {
    size: 12,
    title: 'Expected start date',
    slug: 'start_date',
    placeholder: 'DD/MM/YYYY',
    type: 'date',
    min_date: moment().startOf('day').add(1, 'days').toDate(),
    is_required: true,
  }],
}];

const STATUSLIST = {
  CUSTOMIZE: 'customize',
  REVIEW: 'review',
  CHECKOUT: 'checkout',
  PAYMENTPENDING: 'pending_payment',
  DEPOSITED: 'deposited',
  PAID: 'paid',
};

class BusinessRouteContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { formIndex, isMounted } = prevState;
    const {
      formState,
      match,
      history,
      doSetCategoryNavBar,
      partialFlowStatus,
    } = nextProps;

    if (isMounted) {
      const routeResult = routeHandler(routeName, formState.index, formIndex, match, history, partialFlowStatus ? PARTIALFORMSTATES : FORMSTATES);
      if (routeResult && routeResult.formIndex !== null) {
        // Set category or main nav bar depending on page
        doSetCategoryNavBar(routeResult.formIndex !== 0, true);
        return ({
          formIndex: routeResult.formIndex,
        });
      }
    }
    return null;
  }
  constructor(props) {
    super(props);

    const {
      history,
      doSetQuoteData,
    } = this.props;

    this.state = {
      formIndex: 0,
      values: {},
      errors: {},

      pageForms: null, // All the forms for this flow
      currPage: null, // Current page slug
      allFields: null, // All the fields for this flow

      transitionClass: 'flow-visible',

      showLoading: false,
      showLoadingError: false,

      token: null,
      paymentIntent: null,
      quoteData: null,

      promoCode: null,

      isFromSession: history && history.location && history.location.pathname === '/business/review',

      partialFlowStatus: null, // What is the status when it is deposited or paid
      formStatesToUse: FORMSTATES, // Partial flow or full flow
    };

    doSetQuoteData(null);

    // Reset route
    // history.replace(`/business${history.location.search}`);

    this.renderStepProgress = this.renderStepProgress.bind(this);
    this.renderFlow = this.renderFlow.bind(this);
    this.onChangeValue = this.onChangeValue.bind(this);
    this.onInitalSetup = this.onInitalSetup.bind(this);
    this.validateForm = this.validateForm.bind(this);
    this.setFormIndex = this.setFormIndex.bind(this);

    this.showLoading = this.showLoading.bind(this);
    this.showLoadingError = this.showLoadingError.bind(this);
    this.handleTransition = this.handleTransition.bind(this);

    this.getCurrentFormIndex = this.getCurrentFormIndex.bind(this);

    this.getNewQuote = this.getNewQuote.bind(this);
    this.handleAPI = this.handleAPI.bind(this);

    this.checkOtp = this.checkOtp.bind(this);

    this.quoteButtonCallback = this.quoteButtonCallback.bind(this);
  }

  async componentDidMount() {
    const {
      doSetCategoryNavBar,
      setFormState,
      doSetGetQuoteNavbar,
    } = this.props;

    // Set to main first
    setFormState(FORMSTATES.MAIN);

    this.setState({
      isMounted: true,
    });

    // Set nav bar to product flow
    doSetCategoryNavBar(false, true);
    // For the nav bar to go from landing page to purchase flow
    doSetGetQuoteNavbar(() => this.quoteButtonCallback(true));

    // Do initial setup
    this.onInitalSetup();
  }

  componentWillUnmount() {
    const { doSetCategoryNavBar, doSetQuoteData } = this.props;
    // Clear any redux state
    doSetCategoryNavBar(false, false);
    doSetQuoteData(null);
  }

  // Init the flow with previous session if needed
  async onInitalSetup() {
    const { isFromSession } = this.state;
    const {
      history,
      setFormState,
      doSetQuoteData,
    } = this.props;

    // Try to parse search params from url if from special link
    const searchParams = {};
    try {
      if (isFromSession) {
        const splitParams = history.location.search.split('?')[1].split('&');
        _.each(splitParams, (param) => {
          try {
            const splitParam = param.split('=');

            // eslint-disable-next-line
            searchParams[splitParam[0]] = splitParam[1];
          } catch (error) {
            // eslint-disable-next-line
          }
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    let pageForms = null;
    let currPage = null;
    let allFields = null;
    let values = {};
    let partialFlowStatus = false;
    let formStatesToUse = FORMSTATES;

    try {
      // Get session for this flow
      const session = searchParams.session ? await this.handleAPI(() => getSession(searchParams.session)) : null;
      // Session status
      const initialSessionStatus = session && session.status;
      // Get status for partial flow
      partialFlowStatus = initialSessionStatus === 'deposited' || initialSessionStatus === 'paid' ? initialSessionStatus : null;
      // Which flow to use
      formStatesToUse = partialFlowStatus ? PARTIALFORMSTATES : FORMSTATES;

      // Get all the forms for this flow
      const pages = await this.handleAPI(getForm);

      if (pages) {
        // All the field options for this flow
        allFields = [
          ...getFieldsFromForms(pages),
          emailField,
          ...getFieldsFromForms(checkoutFields),
        ];

        // All the pages
        pageForms = pages;
        if (pageForms) {
          // Set to first page first
          currPage = pageForms[0].slug;
          // If session got latest page, make sure its valid and set to currPage
          if (session && session.page) {
            const isValidPage = _.find(pageForms, form => form.slug === session.page);
            if (isValidPage) {
              currPage = session.page;
            }
          }
        }

        // Set the flow to the previous stage
        if (session && session.status) {
          // Whether need to continue push states
          let isPushMore = true;

          // Set token and payment intent from OTP if necessary
          await new Promise(resolve => this.setState({
            token: searchParams.session,
            paymentIntent: searchParams.payment_intent,
          }, resolve));

          setFormState(formStatesToUse.CUSTOMIZE);
          history.push(`/business${formStatesToUse.CUSTOMIZE.path}${history.location.search}`);
          if (session.status === STATUSLIST.CUSTOMIZE) {
            isPushMore = false;
          }

          if (isPushMore) {
            setFormState(formStatesToUse.REVIEW);
            history.push(`/business${formStatesToUse.REVIEW.path}${history.location.search}`);
            if (session.status === STATUSLIST.REVIEW) {
              isPushMore = false;
            }
          }

          // Only go back to checkout view if there is a payment intent
          if (searchParams.payment_intent) {
            setFormState(formStatesToUse.CHECKOUT);
            history.push(`/business${formStatesToUse.CHECKOUT.path}${history.location.search}`);
          }
        }

        // Restore all inputs or manually add email to values
        // if redirect from PPS landing page
        if (session && session.input_data) {
          const inputData = session.input_data;
          values = _.reduce(inputData, (result, value, key) => {
            const newValues = { ...result };

            // Find this field first
            const field = _.find(allFields, f => f.slug === key);
            if (field) {
              let valueToUse = value;
              // Set back the type
              if (field.type === 'date' || field.type === 'end_date') {
                valueToUse = new Date(value);
              }
              newValues[key] = { type: field.type, value: valueToUse };
            }

            return newValues;
          }, {});

          // Get summary if is partial flow, else get quote WITHOUT promocode
          // Since we do not hold the code for them
          if (partialFlowStatus && searchParams.session) {
            const summaryResult = await getSummary(searchParams.session);
            if (summaryResult && summaryResult.quote) {
              await new Promise((r) => { this.setState({ quoteData: summaryResult.quote }, r); });
              doSetQuoteData({
                quote: { info: summaryResult.info, quote: summaryResult.quote },
                isBalance: summaryResult.quote && summaryResult.quote.outstanding_premium && summaryResult.quote.amount_paid,
              });
            }
          } else if (!_.isEmpty(values)) {
            await this.getNewQuote(allFields, values, searchParams.session, 1);
          }
        }
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    // Set all the necessary stats
    this.setState({
      pageForms,
      currPage,
      values,
      allFields,
      partialFlowStatus,
      formStatesToUse,
      isFromSession: false,
    });
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const {
      values,
      errors,
      allFields,
      token,
      formIndex,
      promoCode,
      formStatesToUse,
    } = this.state;

    try {
      const error = errors && errors[name];
      let newError = {};

      if (error) {
        newError = { [name]: await validateFormValue(name, { type, value }, values, allFields) };
      }

      return new Promise((resolve) => {
        this.setState({
          values: {
            ...values,
            [name]: { type, value },
          },
          errors: { ...errors, ...newError },
        }, async () => {
          const { values: newValues } = this.state;
          // console.log(newValues, this.state.errors);
          if (formIndex === formStatesToUse.CUSTOMIZE.index) {
            if (_.isEmpty(newError)) {
              if (name !== 'age_group') {
                const ageGroup = newValues && newValues.age_group && newValues.age_group.value;
                if (ageGroup) {
                  const employeeCount = newValues && newValues.no_of_employees && newValues.no_of_employees.value >= 0;
                  if (employeeCount) {
                    // Get quote only when there is valid age group
                    await this.getNewQuote(allFields, newValues, token, 1, promoCode);
                  }
                }
              }
            }
            // this.onChangeAgeGroup();
          }
          resolve();
        });
      });
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  setFormIndex(formState) {
    if (formState) {
      const { doSetCategoryNavBar, history, setFormState } = this.props;
      doSetCategoryNavBar(formState.index !== 0, true);
      setFormState(formState);
      history.push(`/${routeName}${formState.path}`);
    }
  }

  async getNewQuote(allFields, values, token, productID, promoCode) {
    try {
      const { doSetQuoteData } = this.props;
      const requestData = generateRequestData(allFields, values);

      let newQuote = null;
      if (requestData.no_of_employees && requestData.no_of_employees > 0) {
        newQuote = await postQuote(requestData, token, productID, promoCode);
      }

      await new Promise((r) => { this.setState({ quoteData: newQuote }, r); });
      doSetQuoteData({ quote: newQuote, isDeposit: false });

      return newQuote;
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  getCurrentFormIndex() {
    const { pageForms, currPage } = this.state;

    let currPageIndex = -1;
    try {
      currPageIndex = _.findIndex(pageForms, form => form.slug === currPage);
    } catch (error) {
      // eslint-disable-next-line
    }

    return currPageIndex;
  }

  async checkOtp() {
    const { paymentIntent, token } = this.state;

    try {
      const optResult = await this.handleAPI(() => (
        new Promise((resolve) => {
          let retryCount = 5;
          let isSuceed = false;
          let mutex = false;

          this.checkOtpInterval = setInterval(async () => {
            if (!mutex) {
              mutex = true;

              const result = await verifyOTP(token, paymentIntent);
              retryCount -= 1;

              isSuceed = result && result.status === 'succeeded';
              if (retryCount === 0 || isSuceed) {
                clearInterval(this.checkOtpInterval);
                resolve(isSuceed);
                return;
              }

              mutex = false;
            }
          }, 1000);
        })
      ));

      this.setState({
        paymentIntent: null,
      });

      return optResult;
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  async handleAPI(apiCallback, overwriteWarning = false) {
    const { isFromSession } = this.state;

    try {
      clearInterval(this.showLoadigInterval);
      if (!isFromSession) {
        this.showLoadigInterval = setInterval(() => this.setState({ showLoading: true }), 300);
      }

      const result = await apiCallback();

      clearInterval(this.showLoadigInterval);
      this.setState({
        showLoadingError: overwriteWarning ? false : !result,
        showLoading: overwriteWarning ? false : !result,
      });
      return result;
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  async showLoading(show) {
    await new Promise((resolve) => { this.setState({ showLoading: show }, resolve); });
  }

  async showLoadingError(show) {
    await new Promise((resolve) => { this.setState({ showLoadingError: show }, resolve); });
  }

  async handleTransition(callback) {
    await this.setState({
      transitionClass: 'flow-invisible',
    }, async () => {
      await new Promise((resolve) => { setTimeout(() => { resolve(); }, 200); });
      callback();
      await new Promise((resolve) => { setTimeout(() => { resolve(); }, 100); });
      this.setState({
        transitionClass: 'flow-visible',
      });
    });
  }

  async quoteButtonCallback(failScrollUp) {
    const { values, pageForms, formStatesToUse } = this.state;

    try {
      const validateResult = await this.validateForm([emailField.slug], async () => {
        const newToken = await postSession(generateRequestData([emailField], values));
        if (await this.handleAPI(() => putSessions(generateRequestData([emailField], values), newToken, STATUSLIST.CUSTOMIZE, pageForms[0].slug))) {
          this.setState({
            token: newToken,
          }, () => {
            this.handleTransition(() => {
              window.scrollTo(0, 0);
              this.setFormIndex(formStatesToUse.CUSTOMIZE);
            });
          });
        }
      });
      // const data = {};
      // const newToken = await postSession(data);
      // if (await this.handleAPI(() => putSessions(data, newToken, STATUSLIST.CUSTOMIZE, pageForms[0].slug))) {
      //   this.setState({
      //     token: newToken,
      //   }, () => {
      //     this.handleTransition(() => {
      //       window.scrollTo(0, 0);
      //       this.setFormIndex(formStatesToUse.CUSTOMIZE);
      //     });
      //   });
      // }

      if (failScrollUp && !validateResult) {
        window.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth',
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  async validateForm(subset = null, callback = null, overwriteFields = null) {
    const {
      values,
      errors,
      allFields,
    } = this.state;

    const fieldsToUse = overwriteFields || allFields;

    let newErrors = { ...errors };
    try {
      newErrors = await validateForm(values, subset, fieldsToUse);
    } catch (error) {
      // eslint-disable-next-line
    }

    return new Promise((resolve) => {
      this.setState({
        errors: newErrors,
      }, () => {
        if (_.isEmpty(newErrors)) {
          if (callback) {
            callback();
          }
          resolve(true);
        }
        resolve(false);
      });
    });
  }

  renderStepProgress() {
    const { formIndex, partialFlowStatus, formStatesToUse } = this.state;

    const inputList = partialFlowStatus ? [
      'Review',
      'Checkout',
    ] : [
      'Customize',
      'Review',
      'Checkout',
    ];

    const stateList = partialFlowStatus ? [
      formStatesToUse.REVIEW,
      formStatesToUse.CHECKOUT,
    ] : [
      formStatesToUse.CUSTOMIZE,
      formStatesToUse.REVIEW,
      formStatesToUse.CHECKOUT,
    ];
    const stateDescriptions = partialFlowStatus ? [
      { main: '' },
      { main: '' },
    ] : [
      { main: '' },
      { main: '' },
      { main: '' },
    ];

    return (
      <StepProgressBar
        className="business-step-progress"
        maxWidth="244px"
        formState={formIndex > formStatesToUse.CHECKOUT.index ? formStatesToUse.CHECKOUT.index : formIndex}
        inputList={inputList}
        formStates={formStatesToUse}
        stateList={stateList}
        stateDescriptions={stateDescriptions}
        charged={false}
        v3Slider
      />
    );
  }

  renderFlow() {
    const {
      formIndex,
      values,
      errors,
      pageForms,
      currPage,
      transitionClass,
      allFields,
      token,
      promoCode,
      paymentIntent,
      isFromSession,
      partialFlowStatus,
      formStatesToUse,
    } = this.state;

    const {
      userDetails,
      doSetQuoteData,
      quoteDataFromState,
      setFormState,
      history,
    } = this.props;

    const isLoggedIn = !_.isEmpty(userDetails);
    const currPageIndex = this.getCurrentFormIndex();

    switch (formIndex) {
      case formStatesToUse.CUSTOMIZE.index:
        return (
          <BusinessForm
            handleAPI={this.handleAPI}
            isFromSession={isFromSession}
            stickyInfo={quoteDataFromState}
            values={values}
            errors={errors}
            onChangeValue={this.onChangeValue}
            validateForm={this.validateForm}
            pageForms={pageForms}
            currPage={currPage}
            isLoggedIn={isLoggedIn}
            headerImage={currPageIndex === 0 ? FlowHeaderImg : null}
            onNext={async () => {
              const newCurrPageIndex = this.getCurrentFormIndex();
              const isInCustomize = newCurrPageIndex + 1 < pageForms.length;
              const newPage = isInCustomize ? pageForms[newCurrPageIndex + 1].slug : currPage;
              const newState = isInCustomize ? STATUSLIST.CUSTOMIZE : STATUSLIST.REVIEW;

              const isFormUpdated = await this.handleAPI(() => putSessions(generateRequestData(allFields, values), token, newState, newPage, 1, partialFlowStatus));
              if (isFormUpdated) {
                this.handleTransition(() => {
                  window.scrollTo(0, 0);
                  try {
                    if (isInCustomize) {
                      this.setState({
                        currPage: newPage,
                      });
                    } else {
                      this.setFormIndex(formStatesToUse.REVIEW);
                    }
                  } catch (error) {
                    // eslint-disable-next-line
                  }
                });
              }
            }}
            onBack={async () => {
              const newCurrPageIndex = this.getCurrentFormIndex();
              const newPage = pageForms[newCurrPageIndex - 1].slug;
              const isFormUpdated = await this.handleAPI(() => putSessions(generateRequestData(allFields, values), token, STATUSLIST.CUSTOMIZE, newPage, 1, partialFlowStatus));

              if (isFormUpdated) {
                this.handleTransition(() => {
                  window.scrollTo(0, 0);
                  try {
                    if (newCurrPageIndex - 1 >= 0) {
                      this.setState({
                        currPage: newPage,
                      });
                    }
                  } catch (error) {
                    // eslint-disable-next-line
                  }
                });
              }
            }}
            transitionClass={transitionClass}
          />
        );
      case formStatesToUse.REVIEW.index:
      case formStatesToUse.CHECKOUT.index:
      case formStatesToUse.SUBMITTED.index:
        return (
          <BusinessCheckout
            setStateToSubmitted={() => {
              setFormState(formStatesToUse.SUBMITTED);
              history.push(`/business${formStatesToUse.SUBMITTED.path}${history.location.search}`);
            }}
            partialFlowStatus={partialFlowStatus}
            postComeBackLater={async params => this.handleAPI(() => postComeBackLater(params, token))}
            doSetQuoteData={doSetQuoteData}
            stickyInfo={quoteDataFromState}
            isFromSession={isFromSession}
            checkOtp={this.checkOtp}
            paymentIntent={paymentIntent}
            handleAPI={this.handleAPI}
            promoCode={promoCode}
            token={token}
            postBankTransfer={async params => this.handleAPI(() => postBankTransfer(params, token))}
            getSummary={async () => this.handleAPI(() => getSummary(token))}
            postCheckout={async (promo = null, isPut = false) => {
              const data = await this.handleAPI(() => postCheckout(token, promo), !isPut);
              if (data) {
                const newQuoteData = data.quote ? {
                  quoteData: {
                    ...(this.state.quoteData || {}),
                    quote: {
                      ...data.quote,
                    },
                  },
                } : {};
                await new Promise((r) => {
                  this.setState({
                    promoCode: promo,
                    ...newQuoteData,
                  }, r);
                });
              }

              if (!isPut) {
                return data;
              }

              if (isPut) {
                await this.handleAPI(() => putSessions(generateRequestData(allFields, values), token, STATUSLIST.PAYMENTPENDING, currPage, 1, partialFlowStatus));
                if (data && data.payment_template_links) {
                  return data;
                }
              }

              return null;
            }}
            checkoutFields={checkoutFields}
            isCheckout={formIndex === formStatesToUse.CHECKOUT.index}
            values={values}
            errors={errors}
            onChangeValue={this.onChangeValue}
            validateForm={this.validateForm}
            isLoggedIn={isLoggedIn}
            onBack={async () => {
              const status = formIndex === formStatesToUse.REVIEW.index ? STATUSLIST.CUSTOMIZE : STATUSLIST.REVIEW;
              const newFormIndex = formIndex === formStatesToUse.REVIEW.index ? formStatesToUse.CUSTOMIZE : formStatesToUse.REVIEW;

              const isFormUpdated = await this.handleAPI(() => putSessions(generateRequestData(allFields, values), token, status, currPage, 1, partialFlowStatus));
              if (isFormUpdated) {
                this.handleTransition(() => {
                  window.scrollTo(0, 0);
                  this.setFormIndex(newFormIndex);
                });
              }
            }}
            onNext={formIndex === formStatesToUse.REVIEW.index ? async () => {
              const isFormUpdated = await this.handleAPI(() => putSessions(generateRequestData(allFields, values), token, STATUSLIST.CHECKOUT, currPage, 1, partialFlowStatus));
                if (isFormUpdated) {
                this.handleTransition(() => {
                  window.scrollTo(0, 0);
                  this.setFormIndex(formStatesToUse.CHECKOUT);
                });
              }
            } : null}
            transitionClass={transitionClass}
            handleTransition={this.handleTransition}
          />
        );
      default:
        return (
          <BusinessLanding
            isFromSession={isFromSession}
            emailField={emailField}
            values={values}
            errors={errors}
            onChangeValue={this.onChangeValue}
            isLoggedIn={isLoggedIn}
            quoteButtonCallback={this.quoteButtonCallback}
            transitionClass={transitionClass}
            handleAPI={this.handleAPI}
          />
        );
    }
  }

  render() {
    const { formIndex, showLoading, showLoadingError } = this.state;

    // const meta = isProduction ? 'PolicyPal, insurance, insurance broker, endowment, endowment plan, savings plan singapore, best endowment plan singapore, 5 years saving plan singapore, endowment fund, endowment insurance, what is endowment plan, best insurance savings plan singapore, endowment policy, ntuc endowment, short term endowment plan singapore, short term endowment plan, best endowment plan, ntuc income endowment, endowment plan comparison, singapore endowment insurance, savings insurance, savings insurance singapore, compare insurance' : '';

    return (
      <Page id="business">
        <Helmet>
          {/* <meta name="keywords" content={meta} /> */}
          <title>PolicyPal 2.0 - Business Insurance</title>
          <meta name="title" content="PolicyPal 2.0 - Business Insurance" />
          <meta name="description" content="PolicyPal Business can offer tailored quotations to protect your business. From corporate companies, to SMEs, startups, we’ve got just the best benefits and coverage for you so you can gain full protection." />
        </Helmet>
        {/* Render step progress only when in flow */}
        { formIndex !== 0 && this.renderStepProgress()}
        {this.renderFlow()}
        <RefreshTimer ref={(node) => {
            if (!this.refresher) {
              node.invokeNewTimer();
            }
            this.refresher = node;
          }}
        />
        <LoadingScreenV2
          isLoading={showLoading}
          isLoadingError={showLoadingError}
          message={<h5>Please try again.</h5>}
          showLoading={this.showLoading}
          showLoadingError={this.showLoadingError}
          noLogos
        />
      </Page>
    );
  }
}

BusinessRouteContainer.defaultProps = {
  userDetails: null,
  quoteDataFromState: null,
};

BusinessRouteContainer.propTypes = {
  userDetails: PropTypes.object,
  // constants: PropTypes.object,
  history: PropTypes.object.isRequired,
  doSetCategoryNavBar: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  doSetGetQuoteNavbar: PropTypes.func.isRequired,
  doSetQuoteData: PropTypes.func.isRequired,
  quoteDataFromState: PropTypes.object,
};

const mapStateToProps = state => ({
  formState: state.insurance.formState,
  constants: state.insurance.constants,
  userDetails: state.userDetails.data,
  quoteDataFromState: state.business.quoteData,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  doSetCategoryNavBar: (isCategory, isBusiness) => {
    dispatch(setCategoryNavBar(isCategory, false, isBusiness));
  },
  doSetGetQuoteNavbar: (callback) => {
    dispatch(setGetQuoteNavbar(callback));
  },
  doSetQuoteData: (data) => {
    dispatch(setQuoteData(data));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(BusinessRouteContainer));
